.el_btn.el_btn__3dClose::before {
  display: inline-block;
  opacity: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -10;
  transform: rotateX(-90deg);
  transform-origin: top center;
  width: 100%;
  height: 100%;
  background-color: #333;
  content: "";
