.swiper-slide {
  height: 500px;
  overflow: hidden;
}

.swiper-slide > img {
  transform: scale(1.3);
  width: 100%;
  height: 100%;
  max-height: 100%;
  object-fit: cover;
  transition: 1.9s;
}

.swiper-slide::after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 61, 125, 0.1);
  content: "";
}

