}

.bl_animationTxt.js_inView .bl_animationTxt_char:nth-child(8) {
  animation-delay: 0.32s;
}

.bl_animationTxt.js_inView .bl_animationTxt_char:nth-child(9) {
  animation-delay: 0.36s;
}

.bl_animationTxt.js_inView .bl_animationTxt_char:nth-child(10) {
